import React, { useState } from 'react';
import { Navbar,Container,Nav,NavDropdown,Button } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import { NavLink, Link, Route, Switch } from 'react-router-dom';
import Detail from './Detail.js';
import axios from 'axios';

function App() {

  let [games, gameschange] = useState(Data);
  let [] = useState([])

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="/">Game Shop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
      </Navbar>
    
    <Switch>

      <Route exact path="/"> 
        <div className="Jumbotron">
        <h1 className="Jumbo-title">최대 50% 세일!</h1>
          <p>
            ??월 ??일부터 ??월 ??일 까지 인기있는 게임들의 할인이 진행됩니다!
          </p>
          <p>
            <Button variant="primary">바로가기</Button>
          </p>
        </div>
        <div>

        </div>

        <div className="place">
            <div className="row">
              {
                games.map((a,i)=>{
                  return <Card games={games[i]} key={i}/>
                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{



              axios.get('https://ranun95.github.io/TestShop/data2.json') //외부에서 데이터 불러오기.
              .then((result)=>{
                console.log(result.data) //get 성공시 출력.
                gameschange( [...games, ...result.data ])
              })
              .catch(()=>{
                console.log('실패.') //get 실패시 출력.
              })
                // axios는 json데이터를 자동으로 고쳐줌.
            }}>더보기</button>
        </div>
      </Route>

      <Route path="/detail/:number"> 
        <Detail games={games} />
      </Route>
        
      <Route path="/:id">
        <div>Nope</div>   
      </Route>

    </Switch>
      {/*<Route path=""></Route>*/}

 

    
    </div>
  );
}

function Card(props){
  return (
    <div className="col-md-4">
      <div className="col-md-4-img">
      <NavLink to={"/detail/"+props.games.id}>
        <img src={props.games.imgurl} width="100%" href="/"/>
      </NavLink>
        </div>
          <h5 className="col-md-4-title">{ props.games.title}</h5>
          <p>판매가 : { props.games.price} 원</p>
      </div>
  )
}

export default App;
