//import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import { NavLink } from 'react-router-dom';

{/*
let 박스 = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상 }
`;
*/}

function Detail(props){

    let [alert, alertchange] = useState(true); //경고창 상태저장.

    useEffect(()=>{

        //axios.get();
        let timer = setTimeout(()=>{ alertchange(false) }, 3000);
        return ()=>{ clearTimeout(timer) }
    },[]);//페이지 첫로딩시에만 작동. ajax쓸때는 [] 필수(첫 업데이트시에만 실행을 위해).

    let {number} = useParams();
    let history = useHistory();
    let SelGames = props.games.find(function(a){
        return a.id == number
    }) // 제품 상세내용 반복으로 불러오되 제품 고유번호 체크.

    return (
      <div className="container">
            
            {/*<박스>
                <제목 색상="blue">Detail</제목>
            </박스>
            */}

            {/*{
                alert === true 
                ? <div className="alert-red">
                    <p>세일 기간이 얼마 남지 않았습니다.</p>
                  </div>
                : null
            }*/}

            <div className="rerow">
              <div className="col-md-6">
                <img src={SelGames.imgurl} width="100%"/>
              </div>

            <div className="selbody">
              <h3 className="seltitle">{SelGames.title}</h3>
              <p>{SelGames.content}</p>
              <p className="selprice">판매가 : {SelGames.price} 원</p>
              <button className="btn btn-danger">구입하기</button>
              &nbsp;
              <button className="btn btn-danger" onClick={()=>{
                  history.goBack();
              }}>뒤로가기</button> 

            </div>
            </div>
          </div> 
    )
  }

  export default Detail;